.header__menu-list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: flex-end;
    column-gap: 24px;
    row-gap: 10px;
    flex-wrap: wrap;
    grid-area: menu;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
  }
  
  @media screen and (max-width: 619px) {
    .header_wrappable .header__menu-list {
      margin: 0;
      width: 100%;
      padding: 40px 30px;
      box-sizing: border-box;
      border-bottom: 1px solid #545454;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 18px;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
    }
  }